.border-btn {
    font-weight: 400;
    font-size: 18px;
    line-height: 110%;
    color: #061F0B;
    padding: 14px 60px;
    border: 2px solid #000000;
    border-radius: 8px;
    transition: all 0.2s linear;

    &:hover {
        background: #012827;
        color: #fff;
    }

    &:active {
        background: #064F4D;
        border-color: #064F4D;
        color: #fff;
    }
}

.fill-btn {
    font-weight: 400;
    font-size: 18px;
    line-height: 110%;
    color: #fff;
    padding: 15px 60px;
    border: 2px solid #012827;
    border-radius: 8px;
    transition: all 0.2s linear;
    background: #012827;

    &:hover {
        background: #064F4D;
        color: #fff;
        border-color: #064F4D;
    }

    &:active {
        background: #064F4D;
        border-color: #064F4D;
        color: #fff;
    }
}