// Общие настройки

.container {
    max-width: 1320px;
    padding: 0px 15px;
    margin: 0 auto;
}

.site-container {
    height: 100%;
}

.site-container {
    display: flex;
    flex-direction: column;
    height: 100%;
}

.page-container {
    flex: 1 0 auto;
}

.footer {
    flex: 0 0 auto;
}

h1 {
    font-weight: 700;
    font-size: 64px;
    line-height: 110%;
}

h2 {
    font-weight: 700;
    font-size: 56px;
    line-height: 110%;
}

h3 {
    font-weight: 700;
    font-size: 48px;
    line-height: 110%;
}

h4 {
    font-weight: 700;
    font-size: 32px;
    line-height: 110%;
}

h5 {
    font-weight: 700;
    font-size: 28px;
    line-height: 110%;
}

h6 {
    font-weight: 700;
    font-size: 20px;
    line-height: 110%;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    color: #09061F;
}


@media(max-width: 991px) {
    h2 {
        font-size: 42px;
    }
}


@media(max-width: 768px) {
    h1 {
        font-size: 36px;
    }

    h2 {
        font-size: 24px;
    }

}