.footer {
    background: #012827;
    padding: 100px 0px;

    @media(max-width: 991px) {
        padding: 50px 0px;
    }

    &__inner {
        display: flex;
        justify-content: space-between;

        @media(max-width: 991px) {
            flex-direction: column;
            align-items: center;
        }
    }

    &__nav {
        display: flex;
        align-items: center;

        @media(max-width: 991px) {
            margin: 50px 0px;
        }

        &-item {
            padding: 0px 20px;
        }

        &-link {
            font-weight: 400;
            font-size: 18px;
            line-height: 110%;
            color: #FFFFFF;
            transition: opacity 0.2s linear;

            @media(max-width: 768px) {
                font-size: 16px;
            }

            &:hover {
                opacity: 0.5;
            }
        }
    }

    &__logo {
        display: flex;
        align-items: center;

        &-img {
            margin-right: 10px;
        }

        &-text {
            font-weight: 700;
            font-size: 25px;
            line-height: 110%;
            color: #FFFFFF;
        }
    }

    .up-btn {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 48px;
        height: 48px;
        background: #000;
        border-radius: 5px;
        background: #fff;
        cursor: pointer;
    }
}