.header {
    padding: 30px 0px;
    border-bottom: 1px solid rgba(0, 0, 0, 0.15);


    @media(max-width: 991px) {
        padding: 19px 0px;
    }

    &__inner {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    &-logo {
        display: flex;
        align-items: center;

        .logo-img {
            img {
                width: 40px;
                height: 40px;
            }

            margin-right: 15px;
        }


        .logo-text {
            font-weight: 700;
            font-size: 25px;
            line-height: 110%;
            color: #061F0B;
        }
    }

    &__nav {
        display: flex;
        align-items: center;

        @media(max-width: 991px) {
            flex-direction: column;
            justify-content: center;
            position: absolute;
            top: 79px;
            background: #fff;
            padding: 30px;
            left: 0;
            transition: 0.2s linear;
            width: 100%;
            opacity: 0;
            visibility: hidden;
            border-bottom: 1px solid rgba(0, 0, 0, 0.15);
        }

        &--opened {
            opacity: 1;
            visibility: visible;
        }

        &-link {
            font-weight: 400;
            font-size: 18px;
            line-height: 110%;
            color: #09061F;
            transition: color 0.2s linear;

            &:hover {
                color: #678188;
            }
        }

        &-item {
            padding: 0px 20px;

            @media(max-width: 991px) {
                margin-bottom: 20px;
                padding-bottom: 10px;
                border-bottom: 1px solid rgba(0, 0, 0, 0.15);
                width: 100%;

                a {
                    width: 100%;
                    display: block;
                }
            }

            &--mobile {
                @media(min-width: 991px) {
                    display: none;
                }
            }
        }
    }

    &__btns {
        display: flex;
        align-items: center;

        @media(max-width: 991px) {
            display: none;
        }

        a:first-child {
            margin-right: 20px;
        }

        .fill-btn {
            padding: 15px 33px;
        }
    }

    .burger-menu {
        @media(min-width: 991px) {
            display: none;
        }

        width: 40px;
        height: 40px;
        position: relative;
        padding: 0;

        span {
            transition: 0.2s linear;

            &:nth-child(1),
            &:nth-child(2),
            &:nth-child(3) {
                display: block;
                height: 3px;
                background: #000;
                width: 100%;
                position: absolute;
            }

            &:nth-child(1) {
                transform: translateY(-10px);
            }

            &:nth-child(3) {
                transform: translateY(10px);
            }
        }

    }
}


.burger-menu.opened {
    span {
        &:nth-child(2) {
            width: 0;
        }

        &:nth-child(1) {
            transform: translate(0) rotate(45deg);
        }

        &:nth-child(3) {
            transform: translate(0) rotate(-45deg);
        }
    }
}


body.header--fixed {
    padding-top: 115px;

    @media(max-width: 991px) {
        padding-top: 79px;
    }

    .header {
        position: fixed;
        top: 0;
        left: 0;
        background: #fff;
        width: 100%;
        z-index: 99;
    }
}