.reserve {
    &__inner {
        display: grid;
        grid-template-columns: repeat(auto-fill, minmax(280px, 300px));
        grid-gap: 30px;
        justify-content: center;

        @media(max-width: 768px) {
            grid-gap: 10px;
        }
    }

    &__item {
        display: flex;
        padding: 10px;
        border: 1px solid rgba(0, 0, 0, 0.15);
        border-radius: 10px;

        &-img {
            margin-right: 10px;
        }

        &-text {
            span {
                display: block;
            }
        }

        &-title {
            font-weight: 400;
            font-size: 20px;
            line-height: 160%;
            color: #09061F;
        }

        &-number {
            font-weight: 400;
            font-size: 16px;
            line-height: 160%;
            color: rgba(9, 6, 31, 0.5);
        }
    }
}