.news {
    .container {
        position: relative;
    }

    &__nav {
        @media(max-width: 991px) {

            &-prev,
            &-next {
                display: none;
            }
        }

        &-wrap {
            display: flex;
            align-items: center;
            position: absolute;
            right: 10px;
            top: 95px;
        }

        &-prev {
            margin-right: 25px;
            cursor: pointer;
            transition: opacity 0.2s linear;

            &:hover {
                opacity: 0.5;
            }
        }

        &-next {
            cursor: pointer;
            transition: opacity 0.2s linear;

            &:hover {
                opacity: 0.5;
            }
        }

        &-disabled {
            opacity: 0.3;
        }
    }


    &__slider {
        width: 100%;





        @media(max-width: 1300px) {
            max-width: 991px;
            margin: 0 auto;
        }

        @media(max-width: 991px) {
            max-width: 615px;
            margin: 0 auto;
        }

        @media(max-width: 768px) {
            max-width: 300px;
        }


        &-wrapper {
            display: flex;
        }

        &-slide {

            .news-preview {
                width: 100%;
                height: auto;

                img {
                    width: 300px;
                    height: auto;
                    object-fit: cover;
                }
            }

            .news-title {
                font-weight: 700;
                font-size: 28px;
                line-height: 130%;
                color: #09061F;
                padding-top: 20px;
                padding-bottom: 5px;
            }

            .news-spoiler {
                font-weight: 400;
                font-size: 18px;
                line-height: 160%;
                color: #09061F;
            }

        }

    }
}