@import 'fonts'; // Шрифты
@import 'reset'; // Сброс стилей
@import 'setting'; // Общие настройки
@import 'vars'; // Переменные
@import 'mixins'; // Миксины




// Компоненты 
@import 'components/footer';
@import 'components/header';
@import 'components/buttons';



// Блоки

@import "blocks/main-screen";
@import "blocks/currency-reserve";
@import "blocks/exchange";
@import "blocks/news";

// Стили



.subtitle {
    font-weight: 400;
    font-size: 16px;
    line-height: 110%;
    letter-spacing: 0.2em;
    text-transform: uppercase;
    color: #123232;
    background: #B0CECD;
    border-radius: 78px;
    padding: 10px;
    margin-bottom: 25px;
    text-align: center;
    max-width: 220px;
}

.section {
    margin-bottom: 120px;

    @media(max-width: 768px) {
        margin-bottom: 100px;
    }



    .subtitle {
        margin-bottom: 30px;
    }

    .section-title {
        text-align: center;
        max-width: 770px;
        margin: 0 auto;
        margin-bottom: 60px;

        @media(max-width: 991px) {
            margin-bottom: 40px;

        }

        @media(max-width: 768px) {
            margin-bottom: 20px;
        }
    }
}




.m-auto {
    margin: 0 auto;
}









@import 'media'; // Должен быть в конце проекта