.exchange {


    .container {
        .section-title {
            margin-bottom: 35px;

            @media(max-width: 768px) {
                margin-bottom: 20px;
            }
        }
    }


    &__text {
        max-width: 900px;
        font-weight: 400;
        font-size: 20px;
        line-height: 160%;
        text-align: center;
        color: #09061F;

        @media(max-width: 768px) {
            font-size: 16px;
        }
    }

    img {
        display: block;
        margin: 0 auto;
        margin-top: -150px;
        position: relative;
        z-index: 1;
        max-width: 800px;
        width: 100%;

        @media(max-width: 768px) {
            margin-top: -80px;
        }

        @media(max-width: 568px) {
            margin-top: -50px;
        }
    }

    &__contact-line {
        position: relative;
        z-index: 5;
        padding: 88px 50px;
        background: #B0CECD;
        border-radius: 30px;
        display: flex;
        justify-content: center;
        margin-top: -280px;

        @media(max-width: 768px) {
            margin-top: -200px;
            padding: 20px !important;
        }

        @media(max-width: 991px) {
            padding: 50px 30px;
        }

        @media(max-width: 568px) {
            margin-top: -50px;
        }

        ul {
            display: flex;
            align-items: center;

            @media(max-width: 768px) {
                flex-direction: column;
            }

            a {
                font-weight: 700;
                font-size: 24px;
                line-height: 130%;
                color: #09061F;

                @media(max-width: 991px) {
                    font-size: 18px;
                }
            }

            li {
                &:not(:last-child) {
                    margin-right: 90px;

                    @media(max-width: 991px) {
                        margin-right: 30px;
                    }

                    @media(max-width: 768px) {
                        margin-right: 0;
                        margin-bottom: 20px;
                    }
                }
            }
        }
    }
}